import { useEffect, useState } from "react";
import { nowPlaying, popular, topRated, upComing } from "../../api";
import { Loading } from "../../components/Loading";
import { Movies } from "./components/Movies";
import { MainBanner } from "./components/MainBanner";
import "swiper/css";
import { Helmet } from "react-helmet-async";
import { PageTitle } from "../../components/PageTitle";

export const Home = () => {
  const [nowData, setNowData] = useState();
  const [popData, setPopData] = useState();
  const [topData, settopData] = useState();
  const [upData, setupData] = useState();
  const [isLoading, setIsLoading] = useState(true); // true( )

  useEffect(() => {
    (async () => {
      try {
        const { results: nowResult } = await nowPlaying();
        const { results: popResult } = await popular();
        const { results: topResult } = await topRated();
        const { results: upResult } = await upComing();

        console.log(popResult);

        setNowData(nowResult);
        setPopData(popResult);
        settopData(topResult);
        setupData(upResult);

        //       (  )  false  
        setIsLoading(false);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  console.log(topData);
  console.log(isLoading);

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
        <>
          <PageTitle title="HOME" />

          <MainBanner data={nowData[0]} />

          <Movies title="  " movieData={nowData} />
          <Movies title=" " movieData={popData} />
          <Movies title=" " movieData={topData} />
          <Movies title="" movieData={upData} />
        </>
      )}
    </>
  );
};

